import { getInitialText } from "./utils";
import { SHOW_MORE, SHOW_LESS } from "./constants";
import "@servicenow/now-rich-text";
import "@servicenow/now-button";

const renderShowMore = description => {
	const showMoreDisplay = {
		initialText: "",
		show_more_display: false
	};
	getInitialText(description, 250, showMoreDisplay);

	if (!showMoreDisplay.show_more_display) {
		return (
			<div className="sc-description">
				<now-rich-text html={description} />
			</div>
		);
	}
	return (
		<div className="initial-text sc-description">
			<now-rich-text html={showMoreDisplay.initialText} />
			<now-button-bare
				label={SHOW_MORE}
				iconEnd="chevron-down-fill"
				variant="primary"
				size="sm"
				hidePadding={true}
			/>
		</div>
	);
};

const renderShowLess = description => {
	return (
		<div className="final-text sc-description">
			<now-rich-text html={description} />
			<now-button-bare
				label={SHOW_LESS}
				iconEnd="chevron-up-fill"
				variant="primary"
				size="sm"
				hidePadding={true}
			/>
		</div>
	);
};

export const view = ({ properties: { description, showMore } }) => {
	if (!description) return null;
	else {
		if (!showMore) {
			return renderShowMore(description);
		} else {
			return renderShowLess(description);
		}
	}
};
