import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import { actions, actionHandlers } from "./actions";
import { view } from "./view";

/**
 * A component which renders the description of a record(ex: catalog item) in rich text format.The components takes in rich text(html)
 * description as a property and renders it accordingly.
 *
 * ```
 * <sn-catalog-descrtiption description={description} showMore=false>
 * </sn-catalog-descrtiption>
 * ```
 *
 * @seismicElement sn-catalog-descrtiption
 * @summary A description component for records(ex: catalog item)
 */

createCustomElement("sn-catalog-description", {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		 * Description of the record/item
		 *
		 * @type {html}
		 */
		description: {
			default: ""
		},
		/**
		 * whether to show complete description of the record/item
		 *
		 * @type {boolean}
		 */
		showMore: {
			default: false
		}
	},
	actions,
	actionHandlers,
	dispatches: {
		/**
		 * Dispatched when text link clicked
		 * @type {{}}
		 */
		LINK_CLICKED: {}
	}
});
