export function getInitialText(description, maxChars, showMoreDisplay) {
	var initialText = "";
	var count = 0;
	var tagsArray = description.match(/<[^> ]+[^>]*>[^<]*/g);
	if (tagsArray) {
		for (var i = 0; i < tagsArray.length; i++) {
			initialText = initialText + tagsArray[i];
			count += getLenth(tagsArray[i]);
			if (count > maxChars) {
				for (var j = i + 1; j < tagsArray.length; j++) {
					if (getLenth(tagsArray[j]) > 0) {
						showMoreDisplay.show_more_display = true;
						break;
					}
				}
				showMoreDisplay.initialText = initialText + "...";
				return;
			}
		}
	} else {
		if (description.length < maxChars) {
			return;
		}
		showMoreDisplay.show_more_display = true;
		showMoreDisplay.initialText = description.substring(0, 250) + "...";
	}
}

function getLenth(tag) {
	var text = tag.replace(/(<([^>]+)>)/gi, "");
	return text.length;
}
